@import url('https://fonts.googleapis.com/css?family=Permanent+Marker');

.main {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  text-align: center;
  color: #210708;
  ffont-family: 'Permanent Marker', cursive;
  max-height: 100%;
}

.player-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 0.5em;
  width: 100%;
}

.player-container table {
  border-collapse: collapse;
  text-align: center;
  width: 25%;
  display: inline-block;
  margin: 2em 2em 0.5em 2em;
  height: 100%;
  max-height:500px;
}

.player-container table td {
  margin: 2em 2em 0.5em 2em;
  padding: 1em 2em;
  border-left: 2em solid #F9FDFF;
  background: #B0B4B9;
  width: 50%;
}

tr:first-child td:first-child {
  border-top-left-radius: 10px;
}
tr:first-child td:last-child {
  border-top-right-radius: 10px;
}
tr:last-child td:first-child {
  border-bottom-left-radius: 10px;
}
tr:last-child td:last-child {
  border-bottom-right-radius: 10px;
}

table td:first-child {
  border-left: none;
 }

img {
  border-radius: 10px;
  border:3px solid #210708;
  max-width: 250px;
  height:250px;
}

.gameplay {
  border: 2px solid #210708;
  border-radius: 10px;
  background: #fff;
  width: 12%;
  position: fixed;
  left: 44%;
  bottom: 5%;
  -ms-flex-pack: center;
      justify-content: center;
  text-align: center;
}

.gameplay table {
  border-collapse: collapse;
  border-radius: 10px;
  background: #B0B4B9;
  display: inline-block;
  padding: 0.5em;
  margin: 0.5em;
}

button {
  display: inline-block;
  -webkit-box-sizing: content-box;
          box-sizing: content-box;
  cursor: pointer;
  padding: 5px;
  border: none;
  border-radius: 10px;
  color: #F9FDFF;
  -o-text-overflow: clip;
     text-overflow: clip;
  background: #210708;
}



